___CSS_LOADER_EXPORT___.push([module.id, "/* -------------------------------------------------------------------\r\n   Popup layer\r\n\r\n   TODO: Clean up order of layers\r\n   ------------------------------------------------------------------- */\r\n.popup-layer {\r\n  pointer-events: none;\r\n  z-index: 10;\r\n  background-color: transparent;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n}\r\n\r\n.popup-layer.dim {\r\n  background-color: #0000007f;\r\n  transition: background-color 0.2s linear;\r\n  pointer-events: all;\r\n}\r\n\r\n.popup-menu {\r\n  background-color: var(--theme-color-bg-3);\r\n}\r\n\r\n.popup-layer-popup {\r\n  background-color: var(--theme-color-bg-4);\r\n  filter: drop-shadow(0 4px 15px var(--theme-color-bg-1-transparent-2));\r\n  box-shadow: 0 0 10px -5px var(--theme-color-bg-1-transparent-2);\r\n  pointer-events: all;\r\n  z-index: 2;\r\n}\r\n\r\n.popup-layer-popup-content {\r\n  position: absolute;\r\n}\r\n\r\n.popup-layer-popup-arrow.top {\r\n  bottom: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-bottom-color: var(--theme-color-bg-3);\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.right {\r\n  left: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-left-color: var(--theme-color-bg-4);\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.bottom {\r\n  top: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-top-color: var(--theme-color-bg-4);\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.left {\r\n  right: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-right-color: var(--theme-color-bg-4);\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.dark {\r\n  border-bottom-color: rgba(0, 0, 0, 0.7);\r\n}\r\n\r\n/** popout **/\r\n\r\n.popup-layer-popout {\r\n  background-color: var(--theme-color-bg-4);\r\n  /* This filter breaks Chromium? causes an internal div in monaco to not be clipped by it's parent correctly, breaking mouse event when resizing */\r\n  /* filter: drop-shadow(0 4px 15px var(--theme-color-bg-1-transparent-2)); */\r\n  box-shadow: 0 5px 20px 5px var(--theme-color-bg-1-transparent);\r\n  pointer-events: all;\r\n  z-index: 2;\r\n  display: inline;\r\n}\r\n\r\n.popup-layer-popout-content {\r\n  display: inline-flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.popup-layer-popout-arrow.top {\r\n  bottom: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-bottom-color: #333;\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.right {\r\n  left: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-left-color: #333;\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.bottom {\r\n  top: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-top-color: #333;\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.left {\r\n  right: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-right-color: #333;\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n/** modal **/\r\n\r\n.popup-layer-modal {\r\n  position: absolute;\r\n  background-color: var(--theme-color-bg-3);\r\n  filter: drop-shadow(0 4px 15px var(--theme-color-bg-1-transparent-2));\r\n  box-shadow: 0 0 10px -5px var(--theme-color-bg-1-transparent-2);\r\n  pointer-events: all;\r\n  z-index: 21;\r\n  top: 50%;\r\n  left: 50%;\r\n}\r\n\r\n.popup-layer-react-modal {\r\n  pointer-events: all;\r\n  z-index: 2;\r\n}\r\n\r\n.popup-layer-popup-menu {\r\n  min-width: 208px;\r\n  cursor: default;\r\n}\r\n\r\n.popup-layer-popup-menu-divider {\r\n  height: 0px;\r\n  border-top: 2px #333 solid;\r\n  margin-top: 3px;\r\n  margin-bottom: 3px;\r\n}\r\n\r\n.popup-layer-toast {\r\n  position: absolute;\r\n  background-color: rgba(0, 0, 0, 0.7);\r\n  border-radius: 3px;\r\n  border-color: rgba(0, 0, 0, 0.7);\r\n  border-width: 3px;\r\n  padding: 5px;\r\n  color: white;\r\n  opacity: 0;\r\n  transition: opacity 500ms;\r\n  font-size: 15px;\r\n  z-index: 90;\r\n}\r\n\r\n.popup-layer-dragger {\r\n  position: absolute;\r\n  background-color: rgba(0, 0, 0, 0.8);\r\n  border-radius: 3px;\r\n  border-width: 3px;\r\n  color: white;\r\n  font-size: 12px;\r\n  -webkit-transition: opacity 0.25s ease-out;\r\n  padding: 5px;\r\n  opacity: 0;\r\n  pointer-events: none;\r\n  z-index: 1;\r\n}\r\n\r\n:root {\r\n  --popup-layer-tooltip-border-color: var(--theme-color-secondary);\r\n  --popup-layer-tooltip-background-color: var(--theme-color-secondary);\r\n}\r\n\r\n.popup-layer-tooltip {\r\n  box-sizing: border-box;\r\n  font-weight: bold;\r\n  background-color: var(--popup-layer-tooltip-background-color);\r\n  border-radius: 2px;\r\n  border-color: var(--popup-layer-tooltip-border-color);\r\n  border-width: 1px;\r\n  padding: 12px 16px;\r\n  color: white;\r\n  position: absolute;\r\n  opacity: 0;\r\n  -webkit-transition: opacity 0.3s;\r\n  pointer-events: none;\r\n  z-index: 2000;\r\n  box-shadow: -4px 4px 16px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n.popup-layer-tooltip-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* These classes are sent in HTML from the viewer */\r\n.popup-layer-tooltip-content h3 {\r\n  font-weight: bold;\r\n  padding: 0;\r\n  font-size: 12px;\r\n  margin: 0;\r\n}\r\n\r\n.popup-layer-tooltip-content p {\r\n  opacity: 0.75;\r\n  margin: 4px 0 0 0;\r\n  padding: 0;\r\n  font-weight: normal;\r\n  max-width: 268px;\r\n}\r\n\r\n.popup-layer-image-row {\r\n  display: flex;\r\n  margin-top: 20px;\r\n  column-gap: 16px;\r\n}\r\n\r\n.popup-layer-image-item {\r\n  display: flex;\r\n  flex-grow: 1;\r\n  flex-direction: column;\r\n}\r\n\r\n.popup-layer-image-item img {\r\n  margin: 0 auto;\r\n}\r\n\r\n.popup-layer-image-item p {\r\n  max-width: 128px; /* width of images */\r\n  text-align: center;\r\n}\r\n\r\n.popup-layer-image-item h3 {\r\n  margin-top: 4px;\r\n  padding: 0;\r\n  font-weight: normal;\r\n  text-align: center;\r\n}\r\n\r\n/* Tooltip arrows */\r\n\r\n.popup-layer-tooltip-arrow {\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-width: 5px;\r\n}\r\n\r\n.popup-layer-tooltip-arrow.bottom {\r\n  bottom: 100%;\r\n  left: 50%;\r\n  border-bottom-color: var(--popup-layer-tooltip-border-color);\r\n  margin-left: -5px;\r\n}\r\n\r\n.popup-layer-tooltip-arrow.left {\r\n  left: 100%;\r\n  top: 50%;\r\n  border-left-color: var(--popup-layer-tooltip-border-color);\r\n  margin-top: -5px;\r\n}\r\n\r\n.popup-layer-tooltip-arrow.top {\r\n  top: 100%;\r\n  left: 50%;\r\n  border-top-color: var(--popup-layer-tooltip-border-color);\r\n  margin-left: -5px;\r\n}\r\n\r\n.popup-layer-tooltip-arrow.right {\r\n  right: 100%;\r\n  top: 50%;\r\n  border-right-color: var(--popup-layer-tooltip-border-color);\r\n  margin-top: -5px;\r\n}\r\n\r\n.popup {\r\n  padding-bottom: 3px;\r\n  color: #aaaaaa;\r\n}\r\n\r\n.popup p {\r\n  padding: 5px;\r\n  margin: 0px;\r\n}\r\n\r\n.popup-group-