r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.left {\r\n  right: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-right-color: var(--theme-color-bg-4);\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n.popup-layer-popup-arrow.dark {\r\n  border-bottom-color: rgba(0, 0, 0, 0.7);\r\n}\r\n\r\n/** popout **/\r\n\r\n.popup-layer-popout {\r\n  background-color: var(--theme-color-bg-4);\r\n  /* This filter breaks Chromium? causes an internal div in monaco to not be clipped by it's parent correctly, breaking mouse event when resizing */\r\n  /* filter: drop-shadow(0 4px 15px var(--theme-color-bg-1-transparent-2)); */\r\n  box-shadow: 0 5px 20px 5px var(--theme-color-bg-1-transparent);\r\n  pointer-events: all;\r\n  z-index: 2;\r\n  display: inline;\r\n}\r\n\r\n.popup-layer-popout-content {\r\n  display: inline-flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.popup-layer-popout-arrow.top {\r\n  bottom: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-bottom-color: #333;\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.right {\r\n  left: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-left-color: #333;\r\n  border-width: 10px;\r\n  margin-top: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.bottom {\r\n  top: 100%;\r\n  left: 50%;\r\n  border: solid transparent;\r\n  content: ' ';\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n  border-top-color: #333;\r\n  border-width: 10px;\r\n  margin-left: -10px;\r\n}\r\n\r\n.popup-layer-popout-arrow.left {\r\n  right: 100%;\r\n  top: 50%;\r\n  border: solid transparent;\r\n  content: ' '